        -ms-transition: ease-in-out .2s;
         -o-transition: ease-in-out .2s;
            transition: ease-in-out .2s;

    border: 1px solid #ececec;
    border-radius: 0;
    background: #fff;
}

.label__checkbox i::after
{
    position: relative;
    top: 3px;
    left: 8px;

    display: block;

    width: 6px;
    height: 10px;
    margin-top: -20px;

    content: '';
