body
{
    font-family: 'opensans-regular-webfont';

    min-width: 1240px;
    max-width: 1920px;
    margin: 0 auto;

    -webkit-font-smoothing: antialiased;
}

.page-list
{
    padding: 20px;
}

.page-list__link
{
    margin: 5px 0;
}

.icon
{
    fill: currentColor;
    fill-rule: evenodd;
}

.page__wrapper
{
    display: -webkit-box;
    display:    -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display:         box;
    display:         flex;
            flex-direction: column;

    max-width: 1920px;
    min-height: 100vh;
    margin: 0 auto;

    -webkit-box-orient: vertical;
       -moz-box-orient: vertical;
         -o-box-orient: vertical;
    -webkit-flex-direction: column;
        -ms-flex-direction: column;
}

.main
{
    -webkit-box-flex: 1;
       -moz-box-flex: 1;
         -o-box-flex: 1;
        -ms-box-flex: 1;
            box-flex: 1;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.header__responsive-menu.active,
.menu.active
{
    display: block;
}

.toggle-menu
{
    position: relative;

    display: none;

    height: 40px;
    margin: 0;
    padding: 0;

    border: none;
    outline: none;
    background: none;
}

.sandwitch
{
    z-index: 200;
    right: 0;
    left: 0;

    width: 31px;
    height: 21px;
    margin: auto;
}

.sandwitch > span
{
    position: relative;

    display: block;

    width: 31px;
    height: 4px;

    border: none;
    border-radius: 0;
    background: #000;
}

.sw-topper
{
    top: 5px;

    display: block;

    -webkit-transition: -webkit-transform .5s, top .2s;
       -moz-transition:    -moz-transform .5s, top .2s;
        -ms-transition:     -ms-transform .5s, top .2s;
         -o-transition:      -o-transform .5s, top .2s;
            transition:         transform .5s, top .2s;
}

.sw-bottom
{
    top: 10px;

    -webkit-transition: -webkit-transform .5s, top .2s;
       -moz-transition:    -moz-transform .5s, top .2s;
        -ms-transition:     -ms-transform .5s, top .2s;
         -o-transition:      -o-transform .5s, top .2s;
            transition:         transform .5s, top .2s;
    -webkit-transition-delay: .2s, 0s;
       -moz-transition-delay: .2s, 0s;
        -ms-transition-delay: .2s, 0s;
         -o-transition-delay: .2s, 0s;
            transition-delay: .2s, 0s;
}

.sw-footer
{
    top: 15px;

    -webkit-transition: all .5s;
       -moz-transition: all .5s;
        -ms-transition: all .5s;
         -o-transition: all .5s;
            transition: all .5s;
    -webkit-transition-delay: .1s;
       -moz-transition-delay: .1s;
        -ms-transition-delay: .1s;
         -o-transition-delay: .1s;
            transition-delay: .1s;
}

.toggle-menu.active .sw-topper,
.button__sub.active .sw-topper
{
    top: 13px;

    -webkit-transform: rotate(-45deg);
       -moz-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
         -o-transform: rotate(-45deg);
            transform: rotate(-45deg);

    background: #e0914c;
}

.toggle-menu.active .sw-bottom,
.button__sub.active .sw-bottom
{
    top: 10px;

    -webkit-transform: rotate(45deg);
       -moz-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
         -o-transform: rotate(45deg);
            transform: rotate(45deg);

    background: #e0914c;
}

.toggle-menu.active .sw-footer,
.button__sub.active .sw-footer
{
    top: 0;

    -webkit-transform: rotate(180deg);
       -moz-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
         -o-transform: rotate(180deg);
            transform: rotate(180deg);

    opacity: 0;
    background: #e0914c;

    -ms-filter: 'progid:DXImageTransform.Microsoft.Alpha(Opacity=0)';
        filter: alpha(opacity=0);
}

.select_custom
{
    font-family: 'opensans-bold-webfont';
    font-size: 13px;

    display: block;

    height: 47px;

    text-transform: uppercase;

    color: #656d78;
    background: #fff;
}

.select_custom.opened,
.select_custom:hover
{
    color: #434a54;
}

.select_custom.opened .jq-selectbox__select,
.select_custom:hover .jq-selectbox__select
{
    -webkit-transition: ease-in-out .2s;
       -moz-transition: ease-in-out .2s;
        -ms-transition: ease-in-out .2s;
         -o-transition: ease-in-out .2s;
            transition: ease-in-out .2s;

    border-color: #7c848e;
}

.select .jq-selectbox__trigger
{
    width: 46px;

    -webkit-box-shadow: -12px 0 9px #fff;
            box-shadow: -12px 0 9px #fff;
}

.select .jq-selectbox__dropdown
{
    width: 100% !important;

    background: #7c848e;
    -webkit-box-shadow: 0 0 3px rgba(0,0,0,.2);
            box-shadow: 0 0 3px rgba(0,0,0,.2);
}

.select .jq-selectbox__select-text
{
    line-height: 47px;

    height: 47px;
    padding-left: 25px;
}

.select .jq-selectbox__trigger
{
    border-left: 1px solid #d1d1d1;
}

.select .jq-selectbox__dropdown li
{
    line-height: 47px;

    height: 47px;
    padding: 0 25px;

    white-space: nowrap;

    color: #fff;
}

.select .jq-selectbox__dropdown li:hover
{
    background: #6b737c;
}

.select .jq-selectbox__dropdown li.selected
{
    color: #fff;
    background: #6b737c;
}

.select .jq-selectbox.disabled
{
    cursor: default;

    color: #9f9f9f;
    border-color: #d1d1d1;
    background: #f9f8f6;
}

.select .jq-selectbox__select
{
    overflow: hidden;

    -webkit-transition: ease-in-out .2s;
       -moz-transition: ease-in-out .2s;
        -ms-transition: ease-in-out .2s;
         -o-transition: ease-in-out .2s;
            transition: ease-in-out .2s;

    border: 1px solid #d1d2db;
}

.select .jq-selectbox.focused .jq-selectbox__select
{
    -webkit-transition: ease-in-out .2s;
       -moz-transition: ease-in-out .2s;
        -ms-transition: ease-in-out .2s;
         -o-transition: ease-in-out .2s;
            transition: ease-in-out .2s;

    border-color: #025958;
}

